/* General */
.App {
  text-align: center;
}

.content {
  margin-top: 20px;
}

/* Estilos para la barra de navegación */
.nav-tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  list-style-type: none;
  padding: 0;
  flex-wrap: wrap; /* Hace que el menú sea responsivo */
}

.nav-tabs li {
  padding: 10px 15px;
  cursor: pointer;
  background-color: #f0f4f8;
  border-radius: 10px;
  margin: 5px;
  transition: background-color 0.3s ease;
}

.nav-tabs li:hover {
  background-color: #aed9cc; /* Color más claro al hacer hover */
}

.nav-tabs li.active {
  background-color: #00796b;
  color: white;
}

.nav-tabs li.active:hover {
  background-color: #00695f; /* Sombreado más oscuro para el botón activo */
}

/* Mejoras para pantallas pequeñas */
@media (max-width: 768px) {
  .nav-tabs li {
    flex-grow: 1; /* Hace que las pestañas ocupen todo el ancho disponible */
    text-align: center;
  }
}
