/* Estilos generales */
body {
    background-color: #f0f4f8;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    text-align: center;
    font-family: 'Roboto', sans-serif;
    background: linear-gradient(135deg, #00796b 30%, #009688 90%);
    border-radius: 15px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
    color: #fff;
}

h1 {
    font-family: 'Lora', serif;
    font-size: 3em;
    margin-bottom: 20px;
    color: #fff;
    font-weight: bold;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.form {
    background-color: #fff;
    padding: 25px;
    border-radius: 12px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 1.2em;
    color: #000000;
}

input[type="text"],
input[type="number"] {
    width: 80%;
    padding: 12px;
    margin-bottom: 20px;
    border: 2px solid #009688;
    border-radius: 8px;
    font-size: 1em;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input:focus {
    outline: none;
    border-color: #009688;
    box-shadow: 0 0 5px #009688;
}

.button-container {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 10px;
}

button {
    padding: 12px 30px;
    background-color: #00796b;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 1.1em;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s, box-shadow 0.2s;
}

button:hover {
    background-color: #004d40;
    transform: translateY(-3px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}

.result {
    margin-top: 20px;
    padding: 15px;
    border: 2px solid #e0e0e0;
    border-radius: 12px;
    background-color: #fff;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
    color: #000000;
}

.result h2 {
    margin-bottom: 10px;
    color: #333;
    font-size: 1.5em;
    font-weight: bold;
}

.result p {
    font-size: 1.3em;
    color: #00796b;
    font-weight: bold;
}

.copy-button {
    padding: 10px 20px;
    background-color: #00796b;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 1.1em;
    font-weight: 500;
    cursor: pointer;
    margin-top: 10px;
}

.copy-button:hover {
    background-color: #004d40;
}

/* Estilos responsive */
@media (max-width: 600px) {
    h1 {
        font-size: 2em;
    }

    .form {
        padding: 20px;
    }

    button {
        padding: 10px 20px;
        font-size: 1em;
    }
}

.select-label {
    display: block;
    margin-bottom: 10px; 
    font-weight: 600;
    font-size: 1em; 
    color: #000000; 
}

.select {
    width: 100%;
    max-width: 400px; 
    padding: 10px; 
    margin-bottom: 20px; 
    border: 2px solid #009688; 
    border-radius: 8px; 
    font-size: 1em; 
    background-color: #fff; 
    transition: border-color 0.3s ease, box-shadow 0.3s ease; 
}

.select:focus {
    outline: none; 
    border-color: #00796b; 
    box-shadow: 0 0 5px rgba(0, 121, 107, 0.5); 
}

.select option {
    padding: 10px; 
}
